:root {
  font-size: 1.35rem;
}
@media only screen and (max-width: 600px) {
  :root {
    font-size: 1.2rem;
  }
}
*,
:after,
:before {
  box-sizing: border-box;
}
html {
  scroll-behavior: smooth;
}
::-webkit-scrollbar {
  width: 15px;
}
::-webkit-scrollbar-track {
  background-color: #121212;
  border-radius: 100vw;
  margin-block: 0.5em;
}
::-webkit-scrollbar-thumb {
  background-color: #2d2d2d;
  border-radius: 100vw;
}
::-webkit-scrollbar-thumb:hover {
  background-color: #3f3f3f;
}
@supports (scrollbar-color: #2d2d2d #121212) {
  * {
    scrollbar-color: #2d2d2d #121212;
  }
}
body {
  margin: 0;
  padding: 5px 0;
  background-color: #000;
  font-family: "Antonio", "Arial Narrow", "Avenir Next Condensed", sans-serif;
  line-height: 1.5;
  color: #c7f;
}
a {
  color: #c3f;
  text-decoration: underline;
}
@keyframes blink {
  0% {
    opacity: 0;
  }
  49% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
}
@keyframes colorchange {
  0% {
    color: #f70;
  }
  25% {
    color: #f70;
  }
  50% {
    color: #f70;
  }
  75% {
    color: #f70;
  }
  80% {
    color: #000;
  }
  90% {
    color: #000;
  }
  100% {
    color: #f5f6fa;
  }
}
.wrap {
  display: flex;
  margin: 0 auto;
  padding: 0 10px 0 5px;
  overflow: hidden;
  max-width: 1340px;
}
#stretch .wrap {
  max-width: 100%;
}
.scroll-top {
  display: block;
}
.scroll-top a {
  display: none;
  width: 190px;
  height: 150px;
  position: fixed;
  bottom: 0;
  text-decoration: none;
  text-transform: uppercase;
  text-align: right;
  border-top: 5px solid #000;
  border-bottom: 5px solid #000;
  padding: 20px 10px 20px 2px;
  background-color: #c8f;
  font-size: 0.96rem;
  font-weight: bold;
  color: #000;
}
.left-frame-top,
.left-frame {
  width: 190px;
  min-width: 190px;
  text-align: right;
  font-size: 0.88rem;
  font-weight: bold;
  color: #000;
}
.left-frame-top {
  background-color: #78f;
  border-radius: 0 0 0 100px;
}
.left-frame-top a,
.left-frame a {
  text-decoration: none;
  color: #000;
}
.left-frame {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding-top: 90px;
  background-color: #d44;
  border-radius: 100px 0 0 0;
}
.panel-1 a {
  display: block;
  background-color: #c8f;
  padding: 82px 10px 15px 0;
  text-decoration: none;
  color: #000;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.panel-1 a:active,
.panel-1 a:focus {
  filter: brightness(70%);
  text-decoration: none;
  border-bottom: none;
  outline: none;
}
.panel-2 {
  padding: 15px 10px 0 0;
}
.right-frame-top {
  flex: 1;
  background-color: #000;
}
.banner {
  padding: 0 0 15px 5px;
  text-align: right;
  text-transform: uppercase;
  line-height: 1.2;
  font-size: clamp(1.2rem, 1.3rem + 2.6vw, 3.1rem);
  color: #f70;
  font-weight: bold;
}
.banner a {
  color: #f70;
  text-decoration: none;
}
@media only screen and (max-width: 500px) {
  .banner {
    padding: 5px 0 10px 3px;
    font-size: 1.2rem;
  }
}
.info-display-container {
  display: flex;
  justify-content: flex-end;
  column-gap: 15px;
}

.title {
  font-size: 2rem;
  margin-right: 112px;
}

@media only screen and (max-width: 610px) {
  .data-cascade-button-group {
    column-gap: 0;
  }
}

.info-display {
  display: flex;
  flex-direction: column;
  width: 100%;
  margin: -24px 0 -200px 24px;
}

.info-display > div {
  height: 10%;
}

.stats-title {
 text-shadow: 1px 0px;
}

.lives-remaining > h4 {
  margin-bottom: 2px;
}

.comm-badge {
  width: 24px;
  margin-top: 24px;
}
.grid,
.grid-2,
.grid-1,
.grid-3,
.grid-4 {
  flex: 1;
  overflow: hidden;
  text-align: right;
  white-space: nowrap;
}
.button-col,
.button-col-1,
.button-col-2 {
  width: 190px;
}
.button-col div:last-of-type,
.button-col-1 div:last-of-type,
.button-col-2 div:last-of-type {
  margin-bottom: -15px;
}
@media only screen and (max-width: 610px) {
  .button-col,
  .button-col-1,
  .button-col-2 {
    width: auto;
    border-left: 15px solid black;
  }
}

.start-game, .reset-game, .instructions {
  display: block;
  width: 190px;
  height: 58px;

  border-radius: 26px;
  background-color: #78f;
  text-align: center;
  line-height: normal !important;
  text-decoration: none;
  text-transform: uppercase;
  font-weight: bold;
  color: #000;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
  font-size: 0.9rem;
  border: none;
}

.start-screen {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}

.start-game-button-container {
  display: flex;
  justify-content: center;
  gap: 12px;
}

.logo {
  max-width: 600px;
  margin-bottom: 24px;
}

.game {
  display: flex;
  gap: 24px;
  flex-wrap: wrap;
  justify-content: space-between;
}

.button#top-left a {
  background-color: #78f;
}
.button#bottom-left a {
  background-color: #f70;
}
.button#top-right a {
  background-color: #ffaa90;
}
.button#bottom-right a {
  background-color: #d44;
}
.button a:active,
.button a:focus {
  filter: brightness(70%);
  border: none;
  outline: none;
  animation: none;
}
@media only screen and (max-width: 500px) {
  .button a {
    height: 44px;
    padding-top: 10px;
    width: 142px;
    border-radius: 20px;
    font-size: 1rem;
  }
}
@media only screen and (max-width: 388px) {
  .button a {
    width: 134px;
    font-size: 0.85rem;
  }
}
.top-corner-bg {
  width: 40px;
  height: 40px;
  background: linear-gradient(to top right, #78f 50%, #000 50%);
}
.top-corner {
