}
.panel-4 {
  padding: 180px 10px 15px 0;
  background-color: #d44;
}
.panel-5 {
  padding: 25px 10px 25px 0;
  background-color: #f70;
}
.panel-6 {
  padding: 230px 10px 15px 0;
  background-color: #ffaa90;
}
.panel-7 {
  padding: 15px 10px 300px 0;
  background-color: #78f;
}
.panel-8 {
  padding: 15px 10px 110px 0;
  background-color: #ffaa90;
}
.panel-9 {
  padding: 15px 10px 170px 0;
  background-color: #d44;
}
.panel-10 {
  border-top: 5px solid #000;
  background-color: #f70;
  padding: 15px 10px 300px 0;
}
.right-frame {
  flex: 1;
  background-color: #000;
}
.corner-bg {
  width: 40px;
  height: 40px;
  background: linear-gradient(to bottom right, #d44 50%, #000 50%);
}
.corner {
  width: 40px;
  height: 40px;
  background-color: #000;
  border-radius: 40px 0 0 0;
}
.content {
  padding: 0 0 25px 50px;
  background-color: #000;
}

.game-over {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}

.instructions-display {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}
.content > *:first-child {
  margin-top: 0;
}
.card {
  max-width: 150px;
  border-radius: 24px;
}
.card > img {
  width: 150px;
  border-radius: 24px;
  margin-bottom: -24px;
}
.card-name {
  margin-bottom: -24px;
  padding: 0 8px;
}

.quote {
  padding: 0 8px;
  padding: 0.5em 10px;
  margin-right: 64px;
}

.quote:before {
  content: open-quote;
  font-size: 3em;
  line-height: 0.1em;
  margin-right: 0.25em;
  vertical-align: -0.4em;
}

.quote:after {
  content: close-quote;
  font-size: 3em;
  line-height: 0.1em;
  margin-right: 0.25em;
  vertical-align: -0.4em;
}

.flexbox {
  display: flex;
  column-gap: 2vw;
  flex-wrap: wrap;
}
.col {
  flex: 1 1 300px;
}
h1,
h2,
h3 {
  margin: 30px 0;
  font-weight: normal;
  line-height: 1.2;
  text-transform: uppercase;
}
h1 {
  font-size: clamp(1.6rem, 1.6rem + 2.7vw, 3.34rem);
  text-align: right;
}
h2 {
  font-size: clamp(1.2rem, 1.2rem + 2vw, 2rem);
}
h3 {
  font-size: clamp(1.1rem, 1rem + 2vw, 1.8rem);
}
p.caption {
  margin-top: 10px;
  margin-bottom: 0;
  text-align: center;
  font-size: 0.88rem;
}
p.indent {
  padding-left: 40px;
}
p.postmeta {
  margin-top: 0;
  text-align: right;
  font-size: clamp(1rem, 0.85rem + 1.4vw, 1.6rem);
  text-transform: uppercase;
}
.logging h1 {
  margin-bottom: 0;
}
code {
  font-size: 0.8rem;
  color: #f70;
}
hr {
  margin: 30px 0;
  height: 6px;
  border: none;
  background-color: #c8f;
  border-radius: 3px;
}
.disc {
  margin: 30px auto;
  width: 20px;
  height: 20px;
  border-radius: 10px;
}
blockquote {
  margin: 30px 40px;
  border-left: 4px solid #c8f;
  padding-left: 20px;
}
.nomar {
  margin: 0 !important;
}
.go-center {
  text-align: center !important;
}
.go-right {
  text-align: right !important;
}
.go-left {
  text-align: left !important;
}
.go-big {
  font-size: 1.3rem;
}
.go-bigger {
  font-size: 1.5rem;
}
.uppercase {
  text-transform: uppercase;
}
.strike {
  text-decoration: line-through;
}
.now {
  white-space: nowrap;
}
.blink-slower {
  animation: blink 3s infinite;
  animation-delay: 1s;
}
.blink {
  animation: blink 2s infinite;
  animation-delay: 1s;
}
.blink-faster {
  animation: blink 1s infinite;
  animation-delay: 1s;
}
a:hover {
  animation: none;
}
.pics-right {
  float: right;
  margin: 10px 0 20px 20px;
}
.pics-left {
  float: left;
  margin: 10px 20px 20px 0;
}
.pics {
  display: block;
  margin: 30px auto 0 auto;
}
.border {
  padding: 10px;
  border: 2px solid #c8f;
}
.lcars-list {
  list-style: none;
}
.lcars-list li {
  position: relative;
  padding: 0 0 5px 42px;
  font-size: 1.3rem;
}
.lcars-list li::before {
  content: "";
  display: block;
  width: 30px;
  height: 20px;
  border-radius: 50%;
  background-color: #c8f;
  position: absolute;
  top: 13px;
  left: 0;
}
@media only screen and (max-width: 600px) {
  .lcars-list {
    margin-left: 0;
    padding-left: 0;
  }
  .lcars-list li::before {
    top: 10px;
  }
}
.buttons {
  margin-top: 35px;
  display: flex;
  flex-wrap: wrap;
}
.jc-space-between {
  justify-content: space-between;
}
.jc-center {
  justify-content: center;
}
.jc-flex-end {
  justify-content: flex-end;
}
.jc-space-around {
  justify-content: space-around;
}
.jc-space-evenly {
  justify-content: space-evenly;
}
.buttons a {
  display: block;
  margin: 0 8px 16px 8px;
  width: 200px;
  height: 60px;
  padding: 24px 25px 0 0;
  background-color: #c8f;
  border-radius: 26px;
  text-align: right;
  line-height: normal;
  text-decoration: none;
  font-size: 22px;
  font-weight: bold;
  text-transform: uppercase;
  color: #000;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.buttons a:active,
.buttons a:focus {
  filter: brightness(70%);
  outline: none;
}
.buttons a.two-rows {
  font-size: 21px;
  padding-top: 2px;
}
.sidebar-buttons a {
  display: block;
  text-decoration: none;
  text-align: right;
  border-bottom: 5px solid #000;
  padding: 20px 10px 20px 2px;
  background-color: #d44;
  text-transform: uppercase;
  color: #000;
}
.sidebar-buttons a:hover {
  filter: brightness(110%);
}
.sidebar-buttons a:active,
.sidebar-buttons a:focus {
  filter: brightness(80%);
  outline: none;
}
@media only screen and (max-width: 500px) {
  .sidebar-buttons a {
    text-align: center;
    padding: 10px 0;
  }
}
.lcars-bar {
  margin: 40px auto;
  height: 20px;
  background-color: #000;
  border-right: 18px solid #94f;
  border-left: 18px solid #94f;
  border-radius: 9px;
  padding: 0 5px;
}
.lcars-bar-inner {
  height: 20px;
  background-color: #c8f;
}
.lcars-bar-slice-top {
  margin: 40px auto -30px auto;
  width: 180px;
  height: 8px;
  background-color: #c8f;
}
.lcars-bar-cutout {
  margin: 0 auto;
  width: 180px;
  height: 10px;
  background-color: #000;
}
.lcars-bar-slice-bottom {
  margin: -30px auto 0 auto;
  width: 180px;
  height: 5px;
  background-color: #c8f;
}
.lcars-text-bar {
  display: flex;
  position: relative;
  margin: 32px 0;
  overflow: visible;
  border-radius: 14px;
  height: 32px;
  background-color: #c8f;
  border-right: 28px solid #94f;
  border-left: 28px solid #94f;
}
.the-end {
  justify-content: flex-end;
}
.lcars-text-bar span {
  position: absolute;
  top: -4px;
  background-color: #000;
  height: 40px;
  overflow: visible;
  padding: 0 10px;
  font-size: 1.64rem;
  line-height: 34px;
  text-transform: uppercase;
  color: #fa4;
}
.lcars-text-bar::before {
  content: "";
  background-color: #000;
  display: block;
  width: 10px;
  height: 32px;
  position: absolute;
  top: 0;
  left: 0;
  overflow: hidden;
}
.lcars-text-bar::after {
  content: "";
  background-color: #000;
  display: block;
  width: 10px;
  height: 32px;
  position: absolute;
  top: 0;
  right: 0;
  overflow: hidden;
}
@media only screen and (max-width: 600px) {
  .lcars-text-bar {
    border-radius: 10px;
    height: 24px;
    border-right-width: 20px;
    border-left-width: 20px;
  }
  .lcars-text-bar span {
    position: absolute;
    top: -6px;
    height: 30px;
    padding: 0 5px;
    font-size: 1.34rem;
    line-height: 32px;
  }
  .lcars-text-bar::before,
  .lcars-text-bar::after {
    width: 5px;
    height: 24px;
  }
}
