footer {
  display: flex;
  bottom: 0;
  margin: 50px auto 0 auto;
  padding: 12px 0;
  background: linear-gradient(#78f 50%, #d44 50%);
  border-radius: 0 20px 20px 0;
  overflow: hidden;
  position: relative;
  z-index: 1;
}
footer::before {
  content: "";
  background-color: #000;
  display: block;
  width: 5px;
  height: 100vh;
  position: absolute;
  top: 0;
  left: 25%;
  overflow: hidden;
}
footer::after {
  content: "";
  background-color: #000;
  display: block;
  width: 2px;
  height: 100vh;
  position: absolute;
  top: 0;
  left: -1px;
  overflow: hidden;
}
.footer-panel {
  width: 12%;
  max-width: 130px;
  height: 100px;
  margin: auto;
  padding: 10px 0 0 1%;
  background-color: #ffaa90;
  border-top: 5px solid #000;
  border-bottom: 5px solid #000;
  font-size: 0.85rem;
  font-weight: bold;
  color: #000;
}
.footer-inside {
  flex: 1;
  min-height: 180px;
  background-color: #000;
  border-radius: 0 20px 20px 0;
  padding: 1.5rem 1rem;
  z-index: 2;
}
.footer-inside::before {
  content: "";
  background-color: #78f;
  display: block;
  width: 15%;
  min-width: 60px;
  height: 10px;
  position: absolute;
  top: 17px;
  left: 1px;
  overflow: hidden;
}
.footer-inside::after {
  content: "";
  background-color: #d44;
  display: block;
  width: 15%;
  min-width: 60px;
  height: 10px;
  position: absolute;
  bottom: 12px;
  left: 1px;
  overflow: hidden;
}
.footer-text {
  display: flex;
  flex-direction: column;
  min-height: 146px;
  align-items: center;
  justify-content: center;
  gap: 20px;
  text-align: center;
  font-size: 0.94rem;
}
.footer-text > * {
  margin: 0;
}
@media only screen and (max-width: 600px) {
  footer::before {
    left: 40%;
  }
}
.footer {
  margin: 0 auto;
  font-size: 0.94rem;
}
